<template>
  <v-dialog width="350px" persistent v-model="editDialog">
    <v-btn fab accent slot="activator">
      <v-icon>edit</v-icon>
    </v-btn>
    <v-card>
      <v-container>
        <v-layout row wrap>
          <v-flex xs12>
            <v-card-title>Edit Funread</v-card-title>
          </v-flex>
        </v-layout>
        <v-layout row wrap>
          <v-flex xs12>
            <v-card-text>
              <v-text-field
              name="title"
              label="ชื่อเรื่อง"
              id="title"
              v-model="editedTitle"
              required></v-text-field>
              <v-text-field
              name="description"
              label="คำอธิบายการ์ตูน"
              id="description"
              multi-line
              v-model="editedDescription"
              required></v-text-field>
            </v-card-text>
          </v-flex>
        </v-layout>
        <v-divider></v-divider>
        <v-layout row wrap>
          <v-flex xs12>
            <v-card-actions>
              <v-btn flat class="blue--text darken-1" @click="editDialog = false">ปิด</v-btn>
              <v-btn flat class="blue--text darken-1" @click="onSaveChanges">บันทึก</v-btn>
            </v-card-actions>
          </v-flex>
        </v-layout>
      </v-container>
    </v-card>
  </v-dialog>
</template>

<script>
  export default {
    props: ['funrun'],
    data () {
      return {
        editDialog: false,
        editedTitle: this.funrun.title,
        editedDescription: this.funrun.description
      }
    },
    methods: {
      onSaveChanges () {
        if (this.editedTitle.trim() === '' || this.editedDescription.trim() === '') {
          return
        }
        this.editDialog = false
        this.$store.dispatch('updateFunrunData', {
          id: this.funrun.id,
          title: this.editedTitle,
          description: this.editedDescription
        })
      }
    }
  }
</script>



